@import url('https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap');

/* Fonts */
$font-pt-sans: 'PT Sans', sans-serif;
$font-one-piece: 'OnePiece';

/* Colors */
$color-background: #e3ecec;
$color-header-gradient: linear-gradient(12deg, rgba(56, 99, 255, 1) 0%, rgba(70, 242, 239, 1) 63%, rgba(255, 255, 255, 1) 100%);
$color-header-text: white;
$color-header-text-shadow: 2px 0 #000, -2px 0 #000, 0 2px #000, 0 -2px #000, 1px 1px #000, -1px -1px #000, 1px -1px #000, -1px 1px #000;
$color-menu-bg: #333;
$color-menu-text: #fff;
$color-menu-text-hover: red;
$color-card-bg: white;
$color-grid-bg: #DECBB6;
$color-grid-text: #444;
$color-footer-bg: #333;
$color-footer-text: white;
$color-btn-bg: blue;
$color-btn-text: white;
$box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

@font-face {
  font-family: $font-one-piece;
  src: url("/src/fonts/op.ttf");
}

* {
  font-family: $font-pt-sans;
}

html {
  background-color: $color-background;
}

/* Capçalera de la plana */
header {
  background: $color-header-gradient;

  h1 {
    color: $color-header-text;
    text-shadow: $color-header-text-shadow;
    text-align: center;
    font-size: 5rem;
    text-transform: uppercase;
    margin: 0;
    font-family: $font-one-piece;
  }
}

/* Contingut de la plana */
body {
  margin: 0;
  position: relative;
  min-height: 100vh;
}

/* Relatiu al menú */
.menu {
  display: flex;
  justify-content: space-around;
  background-color: $color-menu-bg;
  color: $color-menu-text;

  a {
    text-decoration: none;
    color: #fff;
    width: 100%;
    text-align: center;
    padding: 10px 0;
    font-family: 'PT Sans', sans-serif;
    font-size: 1rem;
    font-weight: bold;
    display: flex;
    justify-content: center;

    svg {
      margin-right: 5px;
    }

    &:hover {
      background-color: $color-menu-text-hover;
    }
  }
}

/* Relatiu al contingut principal */
main {
  margin: 0 10px;
  padding-bottom: 13vh;

  > section > article {
    margin-top: 20px;
    background-color: $color-card-bg;
    padding: 20px;
    border-radius: 10px;
    box-shadow: $box-shadow;
  }
}

/* Estis tipus 'card'. */
.card {
  display: flex;

  .card-image, .card-content {
    padding: 10px;
  }

  img {
    width: 124px;
    height: 124px;
    border-radius: 50%;
    object-fit: cover;
    box-shadow: $box-shadow;
  }
}

/* Contenidors de tipus grid per organitzar info en columnes. */
.grid-container {
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(3, 33%);
  background-color: $color-card-bg;
  color: $color-grid-text;

  figure.grid {
    box-shadow: $box-shadow;
    background-color: $color-grid-bg;
    border-radius: 5px;
    padding: 10px;

    img {
      width: 100%;
    }

    p, strong {
      font-family: monospace;
    }

    p.op {
      font-family: 'OnePiece';
      text-transform: uppercase;
      font-weight: bold;
      font-size: 3rem;
      text-align: center;
    }

    p.pt {
      font-weight: bold;
      font-size: 3rem;
      text-align: center;
      font-family: $font-pt-sans;
    }
  }
}

/* Estils del footer */
footer {
  background-color: $color-footer-bg;
  color: $color-footer-text;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 5rem;
  text-align: center;
  font-weight: bold;

  p {
    margin: 15px;
  }
}

a.btn {
  background-color: $color-btn-bg;
  padding: 10px 20px;
  text-align: center;
  color: $color-btn-text;
  font-weight: bold;
  text-decoration: none;
  border-radius: 10px;
}

/* Estils per dispositius mòbils */
@media (max-width: 768px) {
  .menu {
    flex-direction: column;
    align-items: center;
  }

  .card {
    flex-direction: column;
    align-items: center;
  }

  .grid-container {
    grid-template-columns: 100%;
    margin: 10px 0;
  }

  .links {
    text-align: center;
  }
}
